import BaseApiService from './Base'

class Server extends BaseApiService {
    constructor() {
        super();
        this.prefix = process.env.VUE_APP_COURSE_HOST;
        console.log(this.prefix);
    }

    getList = (code) => this.request('/api/courseSystem/listTree', { company_code: code });
    getClassDetail = (id) => this.request('/api/roomCourseWare/detail', { class_id: id });
    addClassDetail = (id, url) => this.request('/api/roomCourseWare/create', { class_id: id , course_ware_url: url});
    updateClassDetail = (id, url) => this.request('/api/roomCourseWare/edit', { class_id: id , course_ware_url: url});
}

export default {
    install(Vue) {
        Vue.prototype.Api = new Server();
    }
}