<template>
  <el-container id="app" :style="companyBackground">
    <iframe id="lessonFrame" allow="autoplay"></iframe>
    <el-button
      v-if="course.userType == 2 && !classData"
      type="danger"
      class="el-icon-s-grid"
      circle
      @click="openHandler"
    ></el-button>

    <el-dialog
      title="课件"
      :visible.sync="visible"
      :width="(error || treeData.length == 0) ? '400px' : '800px'"
    >
      <el-container :style="{'min-height' : (error || treeData.length == 0)  ? '20px' : '280px'}">
        <template v-if="error">{{ error }}</template>
        <template v-else>
          <el-aside v-if="treeData.length" width="220px">
            <el-collapse v-model="treeItem" accordion>
              <el-collapse-item
                v-for="item in treeData"
                :key="item.id"
                :title="item.name"
                :name="item.name"
              >
                <el-tree
                  :ref="'tree_' + item.id"
                  :data="item.children"
                  accordion
                  node-key="id"
                  :props="{label:'name'}"
                  @node-click="treeNodeClickHandler"
                ></el-tree>
              </el-collapse-item>
            </el-collapse>
          </el-aside>
          <el-image v-if="selecteItem && selecteItem.url" :src="selecteItem.cover">
            <span
              slot="error"
              class="el-image__error"
            >{{ selecteItem.cover ? '加载失败': ( selecteItem.children ? '暂无预览' : (selecteItem.name + "(暂无封面)" || '暂无预览')) }}</span>
          </el-image>
          <div v-else style="padding-left:40px">{{treeLoading ? "列表请求中....":"暂无课件"}}</div>
        </template>
      </el-container>
      <span slot="footer" class="dialog-footer">
        <el-button @click="visible = false">取 消</el-button>
        <el-button type="primary" v-if="selecteItem && selecteItem.url" @click="saveHandler">确 定</el-button>
      </span>
    </el-dialog>
  </el-container>
</template>
<script>
import LOGO_BK from './assets/logo_bk.png';
import LOGO_JXH from './assets/logo_jxh.png';
import LOGO_ZHXT from './assets/logo_zhxt.png';
import LOGO_XXDQ from './assets/logo_xxdq.png';
export default {
  name: "App",
  data() {
    return {
      companyType: null,
      classId: null,
      classData: null,
      selecteItem: null,
      treeItem: null,
      treeData: [],
      treeLoading: false,
      visible: false,
      bridge: null,
      course: {
        single: false,
        action: false,
        completed: false, //加载完成
        auth: true,
        userType: 1, //老师2, 学生1
        url: null,
        page: 1,
        total: 1
      },
      companyList: {
        bk: { title: "精锐班课", logo: LOGO_BK },
        jxh: { title: "佳学慧", logo: LOGO_JXH },
        zhxt: { title: "至慧学堂", logo: LOGO_ZHXT },
        xxdq: { title: "小小地球", logo: LOGO_XXDQ }
      },
      error: null
    };
  },
  computed: {
    companyBackground() {
      let data = this.companyType ? this.companyList[this.companyType] : null;
      if (data) {
        document.title += " - " + data.title;
        return { background: 'url(' + data.logo + ') no-repeat center' };
      }
      return null;
    }
  },
  mounted() {
    //doc http://apidoc.it.uuabc.com/index.php?s=/50&page_id=1496
    console.log("init hash -->", window.location.href);
    this.course.userType = this.queryGet('userType') == 1 ? 2 : 1;
    this.classId = [this.queryGet("classId"), this.queryGet("docId")].join("-");
    this.companyType = this.queryGet('company_code');
    if (!this.companyType) {
      this.error = "缺少企业code, 请联系精锐技术!";
    }

    this.loadScript("https://live-cdn.baijiayun.com/js-sdk/tool/BJYBridge.js").then(() => {
      this.init();
      this.Api.getClassDetail(this.classId).then(({ success, data }) => {
        if (success && data) {
          this.classData = data;
          this.course.url = data.url;
          this.loadCourseWare();
        } else {
          this.classData = null;
          if (this.course.userType == 2) {
            this.visible = true;
            this.getList();
          }
        }
      });
    }).catch(() => {

    });
  },
  methods: {
    loadScript(url) {
      return new Promise((resolve, reject) => {
        var xhr = new XMLHttpRequest;
        xhr.open('get', url, true);
        xhr.onreadystatechange = function () {
          if (xhr.readyState == 4) {
            if (xhr.status >= 200 && xhr.status < 300 || xhr.status == 304) {
              var script = document.createElement('script');
              script.type = 'text/javascript';
              script.text = xhr.responseText;
              document.body.appendChild(script);
              resolve();
            }
            reject();
          }
        };

        xhr.onerror = (e) => {
          console.log('error ', url);
          resolve();
        };
        xhr.send();
      });
    },
    init() {
      if (typeof document == 'object') {
        document.domain = this.getCurrentDomain(document.location.hostname)
      }

      this.frame = document.querySelector('#lessonFrame');//获取iframe
      this.loadBridge();
      console.log('init getReady', window.location.hash, this.bridge);
    },
    loadBridge() {
      const that = this;
      console.log("BJYBridge", window.BJYBridge);
      // 获取Bridge单例
      this.bridge = window.BJYBridge.getInstance({
        // 响应由bridge通知而来的翻页指令
        onPageChange: function (page, step) {
          return new Promise(function (resolve) {
            console.log('BJYBridge加载指定页面 --- ', page);
            that.course.page = page;
            if (that.frame && that.frame.contentWindow && that.frame.contentWindow.PageMgr) {
              that.frame.contentWindow.PageMgr.turnPage(page);
            }
            //跳转到指定页
            resolve(step);
          }
          );
        },
        // 响应由bridge通知而来的本页操作记录变化
        onRecordChange: function (record, prevRecord) {
          console.log('onRecordChange', record, prevRecord);
          if (record) {
            let msg = record[record.length - 1];
            const json = JSON.parse(msg);
            if (json.type == 9999) {
              that.changeCourseWare(json.url);
            } else {
              //响应动画
              that.frame.contentWindow.PageMgr.receiveMsg(msg);
            }
          }
        }
      });

      this.bridge.getReady({
        page: 1,
        step: 1,
        pageCount: 1,
        stepCount: 1
      });
    },
    loadCourseWare() {
      if (this.frame.attachEvent) {
        this.frame.attachEvent('onload', this.loadEvent);
      } else {
        this.frame.onload = this.loadEvent;
      }

      // this.frame.src = this.course.url;
      this.frame.src = "https://captive.uuabc.com/v3/index.html";
    },
    changeCourseWare(url) {
      this.course.completed = false;
      this.course.page = 1;
      this.course.url = url;
      this.loadCourseWare();
    },
    loadEvent() {
      const that = this;
      //方法注册
      this.frame.contentWindow.callbackPageLoaded = function (single, action) {
        that.course.single = single;
        that.course.action = action;
        that.course.page = that.frame.contentWindow.PageMgr['cur_page'];
        console.log('加载页面 --- ', that.course);
        // that.bridge.requestChangePage(that.course.page, 1);
      };

      this.frame.contentWindow.callbackSendMsg = function (msg) {
        //共享指令
        // that.bridge.pushRecord(msg);
        that.bridge.replaceRecordStack(msg);
        console.log("action msg", msg);
      };

      this.frame.contentWindow.callbackSendInfo = function (msg) {
        console.log(msg);
      };

      this.frame.contentWindow.callbackResLoaded = function (total) {
        console.log("初始化", that.course, that.frame.contentWindow.PageMgr['all_page']);
        that.course.completed = true;
        that.course.total = total;
        that.frame.contentWindow.GlobalData.userType = that.course.userType;
        that.frame.contentWindow.GlobalData.hasAuthority(that.course.auth);
        that.frame.contentWindow.PageMgr.turnPage(that.course.page);
        that.bridge.getReady({
          page: 1,
          step: 1,
          pageCount: that.frame.contentWindow.PageMgr['all_page'],
          stepCount: 1
        });

        console.log('curseware init completed # getReady', that.bridge);
      }
    },
    queryGet(name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);
      if (r != null)
        return unescape(r[2]);
      return null;
    },
    getCurrentDomain(host) {
      var occurrences = host.split('.');
      var domain = '';

      if (occurrences.length === 1) {
        domain = occurrences.join('.');
      } else if (occurrences.length === 2) {
        domain = occurrences.join('.');
      } else {
        domain = occurrences.splice(occurrences.length - 2).join('.');
      }

      console.log(domain);
      return domain;
    },
    getList() {
      this.treeLoading = true;
      this.Api.getList(this.companyType).then(({ success, data }) => {
        this.treeData = success ? data : [];
        this.error = success ? null : "未知企业, 请联系精锐技术!";
        this.treeLoading = false;
      }).catch(err => {
        this.error = "未知企业, 请联系精锐技术!";
        this.treeLoading = false;
      })
    },
    treeNodeClickHandler(data, node, el) {
      console.log("NODE", data, node, el);
      this.selecteItem = node.data;
    },
    openHandler() {
      this.visible = true;
      if (!this.treeData || this.treeData.length == 0) {
        this.getList();
      }
    },
    saveHandler() {
      // this.bridge.requestChangePage(1, 1);
      if (this.classData) {
        this.Api.updateClassDetail(this.classId, this.selecteItem.url).then(this.changeHandler);
      } else {
        this.Api.addClassDetail(this.classId, this.selecteItem.url).then(this.changeHandler);
      }
    },
    changeHandler({ success }) {
      if (success) {
        this.classData = { id: this.classId, url: this.selecteItem.url };
        this.visible = false;
        this.bridge.replaceRecordStack(JSON.stringify({ type: 9999, url: this.selecteItem.url }));
      }
    }
  }
}
</script>
<style>
html,
body,
iframe,
#app {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
}

#app {
  background: url("./assets/logo.png") no-repeat center;
}

.el-icon-s-grid {
  position: absolute;
  top: 10px;
  left: 10px;
  padding: 5px;
}

.el-icon-s-grid::before {
  font-size: 22px;
  margin: 0;
}

.el-aside {
  width: 220px;
  overflow-y: auto;
  height: 320px;
}

.el-image {
  margin-left: 50px;
  width: 480px;
  height: 320px;
}

.el-collapse-item__header {
  height: 35px !important;
}
</style>